@use "variables" as v;
@use "sass:color";
@use "mixins" as m;
@use "placeholders";

.header {
  width: 100%;
  height: 50px;
  //   background-color: color.adjust(v.$color-blue, $alpha: -0.85);
  background-color: v.$color-blue;
  //   border-bottom: 1px solid v.$color-white;
  border: {
    bottom: {
      width: 1px;
      style: solid;
      color: v.$color-white;
    }
  }

  .nav {
    height: 50px;
    // display: flex;
    // justify-content: start;
    // align-items: start;
    @include m.d-flex(start, center);

    &__title {
      height: 100%;
      width: 255px;
      //   border: {
      //     right: {
      //       width: 1px;
      //       style: solid;
      //       color: v.$color-white;
      //     }
      //   }
      border-right: 1px solid v.$color-white;
      @include m.d-flex(center, center);
      &-link {
        font-size: 25px;
        font-weight: 500;
        color: v.$color-white;
        span {
          font-weight: 700;
        }

        @extend %hover-black;
      }
    }

    &__links {
      flex-grow: 1;

      .list {
        @include m.d-flex(start, center, nowrap, row, min(100px, 5vw), 0);
        padding: 0 0 0 min(100px, 5vw);

        &__item {
          &-link {
            font-size: 22px;
            color: v.$color-white;

            @extend %hover-black;
          }
        }
      }

      @media (max-width: v.$media_lg) {
        .list {
          @include m.d-flex(center, center, nowrap, row, min(100px, 5vw), 0);
          padding: 0;
        }
      }
    }

    .covid {
      height: 100%;
      @include m.d-flex(center, center);
      padding: 0 30px;

      border: {
        left: 1px solid v.$color-white;
        right: 1px solid v.$color-white;
      }

      &__link {
        font-size: 16px;
        font-weight: 700;
        color: v.$color-white;
        @extend %hover-black;
      }
    }

    .glob {
      width: 90px;
      @include m.d-flex(center, center);

      &__link {
        &-icon {
          width: 27px;
          height: 29px;
          fill: v.$color-white;
          @extend %hover-black-icon;
        }
      }
    }
  }
}
