* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
button {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: unset;
}

li {
  list-style: none;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}
